@mixin bg($img, $size: cover, $position: center,  $repeat: no-repeat) {
  background-image: url(../img/#{$img});
  background-position: $position;
  background-repeat: $repeat;
  background-size: $size;
}
::-webkit-input-placeholder {
  color: #fff;
}
