/*animation*/
@keyframes heart {
  50% {
    transform: scale(1.3);
  }
}
@keyframes move {
  to {
    transform: translateX(-100%)
  }
}
@keyframes move2 {
  50% {
    transform: translateX(-100%)
  }
  100% {
    transform: translateX(-200%)
  }
}
@keyframes floats {
  0% {
    transform: translateY(-2%)
  }
  50% {
    transform: translateY(2%)
  }
  100% {
    transform: translateY(-2%)
  }
}
/*animation-end*/

.heart {
  position: absolute;
  left: 50%;
  bottom: 10px;
  margin-left: -35px;
  display: inline-block;
  width: 70px;
  height: 64px;
  animation: heart 1s infinite;
  @include bg("enter@2x.png", contain);
}

/*page*/
#guide, .loginPage {
  @include bg("bg@2x.png");
}
.loginPage {
  .ui-content {
    position: relative;
    z-index: 2;
  }
}
#new {
  @include bg("newBg.png");
  .flex {
    justify-content: space-around;
  }
}

#invade {
  @include bg("invadeBg.png");
  .notice {
    width: 230px;
    height: 47px;
    padding: 20px 40px;
    margin: 0 auto;
    box-sizing: border-box;
    font-size: 12px;
    text-align: center;
    color: #fff;
    @include bg("notice.png");
  }
  
  .venom {
    position: absolute;
    right: -100%;
    top: 10vh;
    width: 100%;
    height: 338px;
    @include bg("venom.png");
    transition: right .2s ease-out;
    animation: floats 1.6s .8s ease-in-out infinite;
  }
  .move {
    right: 0;
  }
  .action {
    position: absolute;
    right: 1em;
    bottom: 20vw;
  }
}
.shade {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .8)
}
.modal {
  display: none;
  
  .inner {
    position: absolute;
    left: 50%;
    top: 50%;
    padding: 20vw 1em;
    box-sizing: border-box;
    transform: translateX(-50%) translateY(-50%);
    @include bg("changeBg.png");
  }
}

#finish {
  @include bg("finishBg.png");
  .nextBtn {
    width: 136px;
    height: 52px;
    margin: 0 auto;
    background-color: transparent;
    border: 0;
    padding: 0;
    @include bg("next-btn.png");
  }

  .endText {
    position: absolute;
    left: 10vw;
    right: 10vw;
    top: 62vh;
    height: 40vw;
    overflow-y: scroll;
    font-size: 10px;
    color: #fff;
    .title {
      text-align: center;
      font-size: 14px;
    }
  }
}

#video {
  @include bg("videoBg.png");
  .ui-content {
    height: 100%;
  }
  .video-item {
    height: 100px;
    overflow: hidden;
    a {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      text-decoration: none;
    }
    img {
      width: 148px;
      height: 80px;
      margin-right: 20px;
    }
    h2, p {
      margin: 0;
    }
    .title {
      margin-bottom: 10px;
      font-size: 16px;
    }
    p {
      margin-bottom: 5px;
    }
    .content {
      flex: 1;
    }
  }
  
}

.header {
  position: relative;
  text-align: center;
  color: #fff;
  .back {
    position: absolute;
    left: 0;
    display: inline-block;
    width: 26px;
    height: 26px;
    @include bg("icon-back.png");
  }
}


.user {
  color: #fff;
  a {
    text-decoration: none;
  }
  @include bg("userBg.png");
  .content {
    margin-top: 40vw;
    font-size: 12px;
  }
  .useraction {
    width: 100%;
    height: 63px;
    @include bg("moneyBg.png");
    a {
      flex: 1;
      padding-top: 36px;
    }
  }
  .menulist {
    li {
      margin-bottom: 10px;
      a {
        padding: 15px 0;
        display: block;
      }
    }
  }
  .paylist {
    a {
      position: relative;
      padding: 15px 0;
      display: block;
      &:after {
        content: '';
        position: absolute;
        right: 1em;
        top: 50%;
        margin-top: -5px;
        display: block;
        width: 10px;
        height: 10px;
        border-right: 2px solid #fff;
        border-bottom: 2px solid #fff;
        transform: rotate(-45deg)
      }
    }
  }
  .out {
    height: 16px;
    @include bg("loginout.png", contain);
  }
}


.ui-body-c {
  border: 0;
  box-shadow: none;
  color: #fff;
  border-radius: 0;
  border-bottom: 1px solid #a7a7a7;
}
.ui-btn-c {
  border: 0;
  background-color: #bd2524;
  color: #fff;
  font-weight: normal;
}

.ui-btn.ui-corner-all {
  border-radius: 20px;
}

// .loginPage .ui-content {
//   padding-top: 225px;
// }
.loginPage form {
  margin-top: 50vw;
}

.code {
  width: 200px!important;
}
.getCode {
  float: right;
}

.info {
  color: #fff;
  p {
    margin: 10px 0;
  }
}

.action {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 156px;
  .rule {
    width: 156px;
    height: 88px;
    @include bg("rules.png");
  }
  .user {
    width: 45px;
    height: 45px;
    margin-bottom: 15px;
    @include bg("icon-user.png");
  }
  .video {
    width: 45px;
    height: 45px;
    @include bg("icon-video.png");
  }
}

.payitem {
  width: 60px;
  height: 70px;
  &.wx {
    @include bg("icon-wx.png");
  }
  &.ali {
    @include bg("icon-alipay.png");
  }
  &.bank {
    @include bg("icon-bank.png");
  }
}
%wrapper {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .8);
}

.pay-wrapper {
  @extend %wrapper;
  @include bg("payBg.png", contain, 0 0);
  .codeimg {
    position: absolute;
    top: 70vw;
    left: 22vw;
    width: 55vw;
    img {
      max-width: 100%;
    }
  }
}

.rule-wrapper {
  @extend %wrapper;
  @include bg("ruleBg.png", contain, 0 0);
  .inner {
    position: absolute;
    top: 58vw;
    left: 70px;
    right: 70px;
    height: 70vw;
    overflow-y: scroll;
    padding-left: 0;
    font-size: 12px;
    color: #fff;
    list-style: none;
    li {
      padding-left: 20px;
      margin-bottom: 10px;
      @include bg("icon-item.png");
      background-size: 7px 14px;
      background-position: left 2px;
    }
    .rpb {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      text-align: center;
      font-size: 10px;
    }
  }
}

