.noneline {
  text-decoration: none;
}
.flex {
  display: flex;
  justify-content: space-between;
}

.flex-between {
  justify-content: space-between!important;
}

.text12{
  font-size: 12px;
}
.arrow {
  position: relative;
  padding-right: 14px;
  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 3px;
    right: 0;
    transform: rotate(45deg);
    width: $arrow-width;
    height: $arrow-width;
    border-top: 2px solid #fff;
    border-right: 2px solid #fff;
  }
}

.pb {
  position: absolute;
  left: 1em;
  right: 1em;
  bottom: 20px;
  // padding: 1em;
}

$i: 5;
@while $i <= 30 {
  .pb-#{$i} { padding-bottom: $i+px; }
  $i: $i + 5;
}
$m: 5;
@while $m <= 30 {
  .mb-#{$m} { margin-bottom: $m+px; }
  $m: $m + 5;
}



.nostyle {
  list-style: none;
  padding-left: 0;
}

.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.text-center {
  text-align: center;
}
.scroll {
  position: absolute;
  top: 14vw;
  left: 1em;
  right: 1em;
  bottom: 1em;
  padding-top: 160px;
  // height: 500px;
  overflow-y: scroll;
}